 Functions\n*. Variables\n*. Mixins\n*. Bootstrap Print Styles\n*. Bootstrap Reboot\n*. Images\n*. Code\n*. Grid\n*. Tables\n*. Forms and Custom Forms\n*. Input Group\n*. Buttons\n*. Button Group\n*. Transitions\n*. Dropdown\n*. Navigation and Tabs\n*. Navbar\n*. Card\n*. Breadcrumbs\n*. Pagination\n*. Badges\n*. Jumbotron\n*. Alert\n*. Progress\n*. Media\n*. List-group\n*. Close\n*. Modal\n*. Tooltip\n*. Popover\n*. Carousel\n*. Utilities\n\n/* Bootstrap Functions */\n/* Variables */\n/* Mixins */\n/* Bootstrap Root Styling and Variables */\n:root {\n  --blue: #3a92ff;\n  --indigo: #6610f2;\n  --purple: #8670f2;\n  --pink: #e83e8c;\n  --red: #eb4c5d;\n  --orange: #fd780a;\n  --yellow: #ffc411;\n  --green: #1cbd35;\n  --teal: #26c1b2;\n  --cyan: #17a2b8;\n  --white: #ffffff;\n  --gray: #868e96;\n  --gray-dark: #343a40;\n  --primary: #8670f2;\n  --secondary: #868e96;\n  --success: #1cbd35;\n  --info: #3a92ff;\n  --warning: #fd780a;\n  --danger: #eb4c5d;\n  --light: #f3f6fa;\n  --dark: #525f6c;\n  --teal: #26c1b2;\n  --indigo: #6610f2;\n  --cyan: #17a2b8;\n  --yellow: #ffc411;\n  --pink: #e83e8c;\n  --breakpoint-xs: 0;\n  --breakpoint-sm: 576px;\n  --breakpoint-md: 768px;\n  --breakpoint-lg: 992px;\n  --breakpoint-xl: 1200px;\n  --font-family-sans-serif: -apple-system, BlinkMacSystemFont, Roboto, \"Helvetica Neue\", Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  --font-family-monospace: \"Roboto Mono\", \"SFMono-Regular\", Menlo, Monaco, Consolas, \"Liberation Mono\", \"Courier New\", monospace; }\n\n/*\nSet the root font size to make the entire UI larger or smaller.\n*/\n:root {\n  font-size: 14px;\n  font-size: 14px; }\n  @media (min-width: 576px) {\n    :root {\n      font-size: 14px; } }\n  @media (min-width: 768px) {\n    :root {\n      font-size: 15px; } }\n  @media (min-width: 992px) {\n    :root {\n      font-size: 15px; } }\n  @media (min-width: 1200px) {\n    :root {\n      font-size: 16px; } }\n\n/* Bootstrap Print Styling */\n@media print {\n  *,\n  *::before,\n  *::after {\n    text-shadow: none !important;\n    box-shadow: none !important; }\n  a:not(.btn) {\n    text-decoration: underline; }\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\"; }\n  pre {\n    white-space: pre-wrap !important; }\n  pre,\n  blockquote {\n    border: 1px solid #999;\n    page-break-inside: avoid; }\n  thead {\n    display: table-header-group; }\n  tr,\n  img {\n    page-break-inside: avoid; }\n  p,\n  h2,\n  h3 {\n    orphans: 3;\n    widows: 3; }\n  h2,\n  h3 {\n    page-break-after: avoid; }\n  @page {\n    size: a3; }\n  body {\n    min-width: 992px !important; }\n  .container {\n    min-width: 992px !important; }\n  .navbar {\n    display: none; }\n  .badge {\n    border: 1px solid #000; }\n  .table {\n    border-collapse: collapse !important; }\n    .table td,\n    .table th {\n      background-color: #fff !important; }\n  .table-bordered th,\n  .table-bordered td {\n    border: 1px solid #ddd !important; } }\n\n/* Bootstrap Reboot */\n*,\n*::before,\n*::after {\n  box-sizing: border-box; }\n\nhtml {\n  font-family: sans-serif;\n  line-height: 1.15;\n  -webkit-text-size-adjust: 100%;\n  -ms-text-size-adjust: 100%;\n  -ms-overflow-style: scrollbar;\n  -webkit-tap-highlight-color: transparent; }\n\n@-ms-viewport {\n  width: device-width; }\n\narticle, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {\n  display: block; }\n\nbody {\n  margin: 0;\n  font-family: -apple-system, BlinkMacSystemFont, Roboto, \"Helvetica Neue\", Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  font-size: 0.875rem;\n  font-weight: 400;\n  line-height: 1.5;\n  color: #576472;\n  text-align: left;\n  background-color: #f3f6fa; }\n\n[tabindex=\"-1\"]:focus {\n  outline: 0 !important; }\n\nhr {\n  box-sizing: content-box;\n  height: 0;\n  overflow: visible; }\n\nh1, h2, h3, h4, h5, h6 {\n  margin-top: 0;\n  margin-bottom: 0.5rem; }\n\np {\n  ma